


















import { Vue, Component, Prop } from "vue-property-decorator";

@Component
export default class HomeWizard extends Vue {
  @Prop({
    type: String,
    default: "mdi-file"
  })
  icon!: string;
  @Prop({
    type: String,
    default: "The title"
  })
  title!: string;
  @Prop({
    type: String,
    default: "The subtitle"
  })
  subTitle!: string;
  @Prop({
    type: [String, Number],
    default: 9999
  })
  number!: string | number;

  @Prop({
    type: String,
    default: ""
  })
  to!: string;
}
